<template>
	<div id="store-comment">
		<public-header :title="store.title" v-if="!isWeixin && !store.headerStyle"></public-header>
		<store-header :store="store" :showRedpacketIcon="showRedpacketIcon" :searchBarOpacity="searchBarOpacity"></store-header>
		<!-- 标签 -->
		<div class="store-tabs flex-lr van-hairline--bottom" :style="{position: (store.headerStyle == 2 ? 'sticky' : 'relative'), top: (store.headerStyle == 2 ? tabsTop : 0) + 'px'}">
			<router-link class="tab-item" :to="util.getUrl({path: '/pages/store/goods?sid=' + store.id})" tag="div">商品</router-link>
			<div class="tab-item active">评价&nbsp;<span class="comment-num">{{store.comment_num}}</span></div>
			<router-link class="tab-item" :to="util.getUrl({path: '/pages/store/index?sid=' + store.id})" tag="div">商家</router-link>
		</div>
		<div class="comment-wrap" :style="{height: (store.headerStyle == 2 ? 'auto' : commentHeight + 'px'), overflowY: (store.headerStyle == 2 ? 'visible' : 'auto')}" v-if="!preLoading">
			<div class="stat van-hairline--bottom">
				<div class="stat-score">
					<div class="score-avg">{{stat.score}}</div>
					<div class="score-rate">
						<p class="score-title">高于{{stat.more_than}}%的商家</p>
						<van-rate v-model="stat.score" :size="12" :disabled-color="util.defaultColor + '!important'" disabled />
					</div>
				</div>
				<div class="score-item">
					<span class="score-title">商品</span>
					<span class="score-value">{{stat.goods_quality}}</span>
				</div>
				<div class="score-item">
					<span class="score-title">配送</span>
					<span class="score-value">{{stat.delivery_service}}</span>
				</div>
				<div class="score-delivery van-hairline--left">
					<span class="score-title">配送满意度</span>
					<span class="score-value"> {{stat.delivery_service_percent}}%</span>
				</div>
			</div>
			<div class="comments">
				<ul class="filters">
					<li class="filter-item" :class="{'active': type == 0}" @click="onChangeType(0)">全部 {{stat.all}}</li>
					<li class="filter-item" :class="{'active': type == 1}" @click="onChangeType(1)">好评 {{stat.good}}</li>
					<li class="filter-item" :class="{'active': type == 2}" @click="onChangeType(2)">中评 {{stat.middle}}</li>
					<li class="filter-item" :class="{'active': type == 3}" @click="onChangeType(3)">差评 {{stat.bad}}</li>
				</ul>
				<div class="read-comment">
					<div class="read-comment-group" @click="onCheckedReadComment">
						<i class="icon" :class="{'icon-check': note == 1}"></i>
						<span>只看有内容的评价</span>
					</div>
				</div>
				<van-list v-model="loading" v-if="!empty" :finished="loaded" @load="onLoad" :offset="100" :immediate-check="false">
					<div class="comment-item" v-for="(item, index) in comments" :key="index">
						<div class="top">
							<img class="avatar bg-img" :src="item.avatar" />
							<div>
								<div class="right-top">
									<h3 class="username">{{item.mobile}}</h3>
									<div class="svip">
										<img class="bg-img" src="https://cube.elemecdn.com/d/88/2a6be34a5202e757fdb480b6deba2png.png" v-if="0" />
									</div>
									<small class="addtime">{{item.addtime}}</small>
								</div>
								<div class="right-bottom">
									<span class="user-rate">
										配送
										<span class="rate-text">{{item.delivery_service_cn}} </span>
									</span>
									<span class="user-rate user-rate-goods">
										商品
										<span class="rate-text">{{item.goods_quality_cn}}</span>
									</span>
									<span class="user-rate" v-if="0">40分钟送达</span>
								</div>
							</div>
						</div>
						<div class="bottom">
							<div class="note" v-if="item.note">{{item.note}}</div>
							<div class="recommend" v-if="item.data.good && item.data.good.length > 0">
								<span class="recommend-item" v-for="(goodItem, goodIndex) in item.data.good" :key="goodIndex">{{goodItem}}</span>
							</div>
							<ul class="thumbs" :class="`thumbs-${item.thumb_col}`" :style="{'--image-cols-of-row': item.thumb_col}" v-if="item.thumbs && item.thumbs.length > 0">
								<li v-for="(thumbItem, thumbIndex) in item.thumbs" :key="thumbIndex" @click="util.jsPreviewImage(item.thumbs, thumbIndex)">
									<img :src="thumbItem" />
								</li>
							</ul>
							<div class="reply" v-if="item.reply">
								<div class="reply-text">商家回复</div>
								{{item.reply}}
							</div>
						</div>
					</div>
				</van-list>
				<van-empty image="https://cube.elemecdn.com/b/f4/f52ec5ee320b5431c914f57be4a34png.png" v-else />
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="preLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import Load from '@/components/load'
	import StoreHeader from '@/components/storeHeader'
	export default {
		name: 'StoreGoods',
		data() {
			return {
				type: 0,
				note: 1,
				commentHeight: 500,
				tabsTop: 63,
				title: '',
				store: {
					is_rest: 0,
					activity: {
						num: 0
					},
					svip: {},
					data: {
						header_style: '0',
					}
				},
				stat: {},
				comments: [],
				min: 0,
				loaded: false,
				loading: false,
				empty: false,
				showRedpacketIcon: false,
				searchBarOpacity: 0,
				preLoading: true
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			Load,
			StoreHeader,
		},
		methods: {
			onLoad(focus) {
				var that = this;
				if(focus) {
					that.min = 0;
					that.comments = [];
					that.loaded = false;
					that.loading = false;
					that.empty = false;
				}
				if(that.loaded) {
					return false;
				}
				that.loading = true;
				that.util.request({
					url: 'wmall/store/comment',
					data: {
						note: that.note,
						type: that.type,
						sid: that.$route.query.sid,
						min: that.min
					}
				}).then((res) => {
					let result = res.data.message;
					that.title = result.message.store.title;
					that.store = result.message.store;
					this.store.headerStyle = window.theme.storeheaderstyle;
					that.stat = result.message.stat;
					that.comments = [...that.comments, ...result.message.comments];
					that.util.setWXTitle(that.store.title)
					if(!that.comments.length) {
						that.loaded = true;
						that.empty = true;
					}
					if(!result.min) {
						that.loaded = true;
					}
					that.loading = false;
					that.min = result.min;
					that.preLoading = false;
					setTimeout(() => {
						let headerHeight = document.getElementById('store-header').clientHeight;
						if(that.store.headerStyle == 2) {
							var searchBarDom = document.getElementsByClassName('search-bar')[0];
							if(searchBarDom) {
								that.tabsTop = searchBarDom.clientHeight;
							}
							window.addEventListener('scroll', function() {
								var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
								if(scrollTop >= that.tabsTop) {
									if(!that.showRedpacketIcon) {
										that.showRedpacketIcon = true;
									}
								} else {
									if(that.showRedpacketIcon) {
										that.showRedpacketIcon = false;
									}
								}
								var sign = headerHeight - that.tabsTop;
								that.searchBarOpacity = scrollTop / sign;
							});
						} else {
							let bodyHeight = document.documentElement.clientHeight;
							let tabsHeight =document.getElementsByClassName('store-tabs')[0].clientHeight;
							that.commentHeight = bodyHeight - headerHeight - tabsHeight;
							if(document.getElementById('public-header')) {
								let publicHeader = document.getElementById('public-header').clientHeight;
								that.commentHeight = that.commentHeight - publicHeader;
							}
						}
					}, 200)
				})
			},
			onChangeType(type) {
				var that = this;
				if(that.type == type) {
					return;
				}
				that.type = type;
				that.onLoad(true);
			},
			onCheckedReadComment() {
				var that = this;
				if(that.note == 1) {
					that.note = 0;
				} else {
					that.note = 1;
				}
				that.onLoad(true);
			}
		},
		mounted () {
			this.onLoad();
		},

		beforeDestroy: function () {
			window.scrollTo(0, 0);
			window.removeEventListener('scroll', function() {}, true);
		}
	}
</script>

<style>
	@import '../../../static/css/diy.css';
	#store-comment{
		background-color: #f5f5f5;
	}
	#store-comment .comment::-webkit-scrollbar {/*滚动条整体样式*/
		width: 0px;   /*高宽分别对应横竖滚动条的尺寸*/
		height: 0px;
	}
	#store-comment .diy-store-list::-webkit-scrollbar {/*滚动条整体样式*/
		width: 0px;   /*高宽分别对应横竖滚动条的尺寸*/
		height: 0px;
	}
	#store-comment #public-header~#store-header{
		margin-top: 46px;
	}
	#store-comment .van-cell__left-icon{
		font-size: 20px;
	}
	#store-comment .store-tabs{
		width: 100%;
		height: 45px;
		line-height: 45px;
		background-color: #fff;
		color: #333;
		z-index: 100;
	}
	#store-comment .store-tabs .tab-item{
		width: 40%;
		height: 100%;
		text-align: center;
		font-size: 14px;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: PingFang SC,miui;
	}
	#store-comment .store-tabs .tab-item.active{
		color: #ff2d4b;
		font-weight: 800;
	}
	#store-comment .store-tabs .tab-item.active:after{
		content: '';
		position: absolute;
		left: 50%;
		top: 75%;
		transform: translateX(-50%);
		width: 22px;
		height: 0;
		border-bottom: 3px solid #ff2d4b;
	}
	#store-comment .store-tabs .tab-item .comment-num{
		font-size: 12px;
		color: #ccc;
	}
	/* 分隔线 */
	#store-comment .fg-line{
		display: inline-block;
		margin: 5px 5px 0 5px;
		width: 0;
		height: 9px;
		font-size: 0;
		border-right: 1px solid #ccc;
	}

	/*评价*/
	#store-comment .comment-wrap{
		width: 100%;
		box-sizing: border-box;
		background: #f4f4f4;
	}
	#store-comment .comment-wrap .stat{
		background-color: #fff;
		padding: 15px;
		font-size: 14px;
		display: flex;
		align-items: center;
		color: #666;
	}
	#store-comment .comment-wrap .stat .score-item,
	#store-comment .comment-wrap .stat .stat-score {
		display: flex;
	}
	#store-comment .comment-wrap .stat .stat-score {
		flex-direction: row;
		flex: 1;
	}
	#store-comment .comment-wrap .stat .score-avg {
		font-family: PingFangSC-Semibold;
		font-size: 30px;
		line-height: 1;
		font-weight: 400;
		color: #ff2d4b;
		font-weight: 700;
		display: block;
		margin-right: 8px;
	}
	#store-comment .comment-wrap .stat .score-title{
		font-family: PingFangSC-Regular;
		font-size: 9px;
		color: #aaa;
		margin-bottom: 6px;
	}
	#store-comment .comment-wrap .stat .score-delivery,
	#store-comment .comment-wrap .stat .score-item {
		flex-direction: column;
	}
	#store-comment .comment-wrap .stat .score-value {
		font-family: PingFangSC-Medium;
		font-weight: 700;
		font-size: 14px;
		color: #333;
	}
	#store-comment .comment-wrap .stat .score-item {
		align-items: center;
		margin-right: 20px;
	}
	#store-comment .comment-wrap .stat .score-delivery {
		font-size: 14px;
		line-height: 1;
		display: flex;
		align-items: flex-start;
		padding-left: 20px;
		position: relative;
	}
	#store-comment .comment-wrap .comments {
		background-color: #fff;
		padding: 10px 12px 0;
		font-size: 14px;
	}
	#store-comment .comment-wrap .filters {
		width: 100%;
	}
	#store-comment .comment-wrap .filter-item {
		display: inline-block;
		padding: 0 8px;
		height: 28px;
		line-height: 28px;
		margin: 4px;
		font-size: 12px;
		border-radius: 2px;
		color: #333;
		background-color: #f5f5f5;
	}
	#store-comment .comment-wrap .filter-item.active {
		color: #fff;
		background-color: #ff2d4b;
	}
	#store-comment .comment-wrap .filter-item img {
		width: 14px;
		height: 11px;
		vertical-align: middle;
		margin-top: -3px;
	}
	#store-comment .comment-wrap .read-comment {
		background-color: #FFF;
	}
	#store-comment .comment-wrap .read-comment .read-comment-group{
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 13px 0 14px;
		font-size: 12px;
		line-height: 1;
		color: #666;
		margin-left: 3.5px;
	}
	#store-comment .comment-wrap .read-comment .read-comment-group .icon{
		width: 14px;
		height: 14px;
		border-radius: 50%;
		border: 1px solid #ccc;
		text-align: center;
		line-height: 14px;
		font-size: 12px;
		margin-right: 5px;
	}
	#store-comment .comment-wrap .read-comment .read-comment-group .icon.icon-check{
		color: #fff;
		background-color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#store-comment .comment-wrap .comment-item {
		padding-bottom: 48px;
		position: relative;
	}
	#store-comment .comment-wrap .comment-item .top {
		position: relative;
		padding-left: 44px;
		line-height: 1.3;
	}
	#store-comment .comment-wrap .comment-item .avatar {
		position: absolute;
		top: 0;
		left: 0;
		width: 32px;
		height: 32px;
		border-radius: 2px;
	}
	#store-comment .comment-wrap .comment-item .right-top {
		display: flex;
		align-items: center;
	}
	#store-comment .comment-wrap .comment-item .username {
		font-size: 16px;
		margin-top: 0;
		color: #191919;
		margin-right: 6px;
		font-weight: 500;
	}
	#store-comment .comment-wrap .comment-item .svip {
		padding: 0 2px;
		flex: 1;
		display: flex;
	}
	#store-comment .comment-wrap .comment-item .svip img {
		width: auto;
		height: 15px;
	}
	#store-comment .comment-wrap .comment-item .addtime {
		font-size: 11px;
		color: #bbb;
	}
	#store-comment .comment-wrap .comment-item .right-bottom {
		position: relative;
		display: flex;
	}
	#store-comment .comment-wrap .comment-item .user-rate:first-of-type {
		padding-left: 0;
	}
	#store-comment .comment-wrap .comment-item .user-rate {
		font-size: 11px;
		color: #aaa;
		display: inline-flex;
	}
	#store-comment .comment-wrap .comment-item .user-rate-goods {
		flex: 1;
		padding-left: 16px;
	}
	#store-comment .comment-wrap .comment-item .user-rate:last-of-type {
		border-right: none;
		align-self: right;
	}
	#store-comment .comment-wrap .comment-item .rate-text {
		margin: auto auto auto 3px;
		color: #666;
	}
	#store-comment .comment-wrap .comment-item .bottom {
		font-size: 13px;
	}
	#store-comment .comment-wrap .comment-item .note {
		color: #333;
		font-size: 13px;
		margin-top: 12px;
		line-height: 1.38;
	}
	#store-comment .comment-wrap .comment-item .recommend {
		margin-top: 6px;
		white-space: break-spaces;
	}
	#store-comment .comment-wrap .comment-item .recommend-item {
		color: #ff2d4b;
		background-color: rgba(255,45,75,.08);
		padding: 2px 3px;
		margin: 0 6px 6px 0;
		display: inline-block;
		word-break: keep-all;
		white-space: nowrap;
	}
	#store-comment .comment-wrap .comment-item .recommend-item {
		font-family: PingFangSC-Regular;
		font-size: 12px;
		border-radius: 3px;
	}
	#store-comment .comment-wrap .comment-item .thumbs {
		--image-cols-of-row: 3;
		margin-top: 10px;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
	}
	#store-comment .comment-wrap .comment-item .thumbs li {
		width: calc((100% - (var(--image-cols-of-row) - 1)*8px)/var(--image-cols-of-row));
		margin-bottom: 6px;
		margin-right: 8px;
		border-radius: 4px;
		overflow: hidden;
	}
	#store-comment .comment-wrap .comment-item .thumbs.thumbs-2 li:nth-child(2n){
		margin-right: 0px;
	}
	#store-comment .comment-wrap .comment-item .thumbs.thumbs-3 li:nth-child(3n){
		margin-right: 0px;
	}
	#store-comment .comment-wrap .comment-item .thumbs img {
		width: 100%;
		height: 100%;
	}
	#store-comment .comment-wrap .comment-item .reply {
		color: #333;
		position: relative;
		margin-top: 7px;
		padding: 15px 12px 12px;
		background: rgba(0,0,0,.02);
		border-radius: 4px;
		font-size: 12px;
		line-height: 18px;
	}
	#store-comment .comment-wrap .comment-item .reply-text {
		font-family: PingFangSC-Medium;
		font-size: 12px;
		color: #333;
		font-weight: 700;
		margin-bottom: 2px;
	}
</style>



